/* Обратите внимание на измения для "липкой" шапки, пометил их звездочкой ниже */
body {
    line-height: 1; }

header {
    position: relative;
    height: 50px; }


nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 2; }


.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;

    &_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300; }

    &_item {
        border-right: 2px dotted black;
        padding-right: 28px; } }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);

    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }

    &_official {
        margin-top: 6px;
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400; }

    &_call {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        margin-top: 19px;
        text-align: right; }

    &_phone {
        display: block;
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }

    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $color_blue 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url('../img/promo_bg.png') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;

    &_header {
        color: #ffffff;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 60px;
        line-height: 60px;
        margin: 0;
        text-align: center; }

    &_subheader {
        color: #ffffff;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 36px;
        line-height: 36px;
        margin: 0;
        margin-top: 22px;
        text-align: center; }

    &_descr {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        margin-top: 23px; }

    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $color_blue 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 8px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }

.title {
    /** line-height: 0 **/
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }

.subtitle {
    margin-top: 19px;
    font-size: 17px;
    font-weight: 400;
    color: #222222;
    text-align: center; }

.label_white {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 8px 12px;
    background-color: white;
    color: black;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center; }

.title_white {
    /** line-height: 0 **/
    margin-bottom: 0;
    margin-top: 38px;
    color: white;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }

.subtitle_white {
    margin-top: 19px;
    font-size: 17px;
    font-weight: 400;
    color: white;
    text-align: center; }

.reasons {
    padding: 71px 0 65px 0;

    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 40px;

        &_round {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 26px;
            width: 113px;
            height: 113px;
            background-color: $color_blue;
            border-radius: 100%; }

        &_descr {
            width: 343px;

            &_subtitle {
                line-height: 21px;
                color: $color_blue;
                font-size: 17px;
                font-weight: 400; }

            &_text {
                color: #202020;
                font-size: 15px;
                font-weight: 300;
                line-height: 21px; } } } }

.mobile {
    min-height: 864px;
    padding: 48px 0 80px 0;
    background: url('../img/white_bg.png') center (center / cover) no-repeat;

    .row {
        margin-top: 32px; }

    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;

        &_1 {
            background: url('../img/icons/mobile_app/mobile_img_1.png') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/icons/mobile_app/mobile_img_2.png') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/icons/mobile_app/mobile_img_3.png') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/icons/mobile_app/mobile_img_4.png') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/icons/mobile_app/mobile_img_5.png') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/icons/mobile_app/mobile_img_6.png') center (center / cover) no-repeat; }

        &_subtitle {
            font-size: 24px;
            line-height: 31px;
            font-weight: 400;
            text-transform: uppercase;
            color: #ffffff;
            text-align: center; }

        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 35px;
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer;
            &:before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $color_blue;
                transition: 0.3s all; }
            &:after {
                content: '';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                height: 14px;
                width: 2px;
                background-color: $color_blue;
                transition: 0.3s all; }
            &:hover {
                &:before, &:after {
                    background-color: $color_red; } } } } }

.choice {
    min-height: 896px;
    background: url('../img/layers_BG.png') center (center / cover) no-repeat;
    padding: 56px 0 138px 0;
    &_car {
        display: flex;
        justify-content: center;
        margin-top: 56px; }
    &_text {
        font-size: 17px;
        line-height: 24px;
        font-weight: 300;
        color: #222222;
        text-align: center;
        margin-top: 4px; } }

.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        font-size: 22px;
        font-weight: 400;
        color: $color_blue; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            color: #222222;
            line-height: 24px;
            margin-bottom: 10px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                width: 21px;
                height: 20px;
                left: -32px;
                top: 3px;
                background: url('../img/icons/small_icons/blue_check_mark.png') center (center / cover) no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: $color_blue; }
    .warning {
        .require_title {
            color: $color_red; }
        .require_list {
            li {
                &:before {
                    background: url('../img/icons/small_icons/red_check_mark.png') center (center / cover) no-repeat; } } } } }

.world {
    position: relative;
    padding: 49px 0;
    background: url('../img/World_bg.png') center (center / cover) no-repeat;
    min-height: 717px;
    &_link {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        bottom: 49px;
        text-align: center;
        font-size: 15px;
        font-weight: 400;
        text-transform: uppercase;
        color: #1fbad6;
        &:hover {
            text-decoration: none;
            color: #fff; } } }

footer {
    padding: 14px 23px 39px 23px;
    a {
        &:hover {
            text-decoration: none; } } }

.footer {
    &_driver {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }
    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-top: 32px; }
    &_wrapper > div {
        width: 33%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }

    &_social {
        width: 154px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;
            i {
                color: white; } } }
    &_link {
        left: 100px;
        width: 390px;
        margin: 0 auto;
        &_main {
           display: flex;
           justify-content: space-between;
           padding: 0 10px;
           a {
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase;
                color: #070716; } }
        &_sub {
            margin-top: 16px;
            display: flex;
            justify-content: space-between;
            a {
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase;
                color: #999999; } }
        &_lung {
            display: block;
            margin: 0 auto;
            margin-top: 19px;
            font-size: 11px;
            font-weight: 400;
            text-transform: uppercase;
            color: #1fbad6;
            text-align: center; } }
    &_mobile {
        width: 296px;
        height: 27px;
        img {
            width: 90px; } } }
